import React, { useEffect, useCallback, useRef } from "react";
import { LazyLoadImage } from "react-lazy-load-image-component";

const { kakao } = window;

const KakaoMap = ({ coordinates, resultIndex }) => {
  const mapRef = useRef();
  //        
  var map = "";
  let CENTER_POINT = "";

  /*     */
  let la = 0; //   
  let lo = 0; //   

  const OPTIONS = {
    enableHighAccuracy: true, //    -  M
    timeout: 1000, //     (5)
    maximumAge: 0, //    
  };

  const MARKER_BOX = [];

  useEffect(() => {
    /*   */
    const mapCoordinates = coordinates[resultIndex].track;
    const MapCenter = coordinates[resultIndex].MapCenter;

    runningCourse(mapCoordinates, MapCenter);
  }, [coordinates, resultIndex]);

  function runningCourse(coordinates, MapCenter) {
    /*   */
    var mapContainer = mapRef.current, //   div
      mapOption = {
        center: new kakao.maps.LatLng(MapCenter.lng, MapCenter.lat), //  
        level: MapCenter.mapDepthLevel, //   
      };

    CENTER_POINT = new kakao.maps.LatLng(MapCenter.lng, MapCenter.lat);

    map = new kakao.maps.Map(mapContainer, mapOption); //  

    //        
    map.setDraggable(false);

    //       ,  
    map.setZoomable(false);

    /*   ,  */
    var linePath = []; //  
    let distanceOverlay = "";

    //   
    for (let i = 0; i < coordinates.length; i++) {
      var circleOverlay = new kakao.maps.CustomOverlay({
        content: '<span class="dot"></span>',
        position: new kakao.maps.LatLng(coordinates[i].lng, coordinates[i].lat),
        zIndex: 1,
      });

      //     
      linePath.push(
        new kakao.maps.LatLng(coordinates[i].lng, coordinates[i].lat)
      );

      //    
      var polyline = new kakao.maps.Polyline({
        path: linePath, //    
        endArrow: true,
        strokeWeight: 7, //   
        strokeColor: "red", //  
        strokeOpacity: 1, //    1 0   0  
        strokeStyle: "solid", //  
      });

      //   
      let distance = Math.round(polyline.getLength());

      //   
      let spotName = coordinates[i].coordinatesName;

      //   ,  
      if (coordinates[i].display === "y") {
        // display y  
        distanceOverlay = new kakao.maps.CustomOverlay({
          map: map, //   
          content:
            '<div class="dotOverlay">' +
            `<div style="text-align:center">` +
            spotName +
            `</div>` +
            ` <span class="number">` +
            Math.round((distance / 1000) * 10) / 10 +
            `</span> km</div>`, //   
          position: new kakao.maps.LatLng(
            coordinates[i].lng,
            coordinates[i].lat
          ), //   .
          xAnchor: 0,
          yAnchor: 0,
          zIndex: 3,
        });
        //  
        circleOverlay.setMap(map);
      }
    }

    //   
    polyline.setMap(map);

    var distance = Math.round(polyline.getLength()), //    
      content = getTimeHTML(distance); //   

    distanceOverlay = new kakao.maps.CustomOverlay({
      map: map, //   
      content: content, //   
      position: new kakao.maps.LatLng(
        coordinates[coordinates.length - 1].lng,
        coordinates[coordinates.length - 1].lat
      ), //   .
      xAnchor: 0,
      yAnchor: 0,
      zIndex: 3,
    });
    //   
    distanceOverlay.setMap(map);

    /* ,    */
    mapContainer.appendChild(content);

    /* ,  */
    var startMarker =
      coordinates[0].lng === coordinates[coordinates.length - 1].lng
        ? new kakao.maps.Point(15, 70)
        : new kakao.maps.Point(15, 45);

    var startSrc =
        "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/red_b.png", //   
      startSize = new kakao.maps.Size(50, 45), //   
      startOption = {
        offset: startMarker, //        (   )
      };

    //    
    var startImage = new kakao.maps.MarkerImage(
      startSrc,
      startSize,
      startOption
    );

    //    
    var startPosition = new kakao.maps.LatLng(
      coordinates[0].lng,
      coordinates[0].lat
    );

    //   
    startMarker = new kakao.maps.Marker({
      map: map, //      
      position: startPosition,
      image: startImage, //   
    });

    startMarker.setImage(startImage);

    var arriveSrc =
        "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/blue_b.png", //   
      arriveSize = new kakao.maps.Size(50, 45), //   
      arriveOption = {
        offset: new kakao.maps.Point(15, 45), //        (   )
      };

    //    
    var arriveImage = new kakao.maps.MarkerImage(
      arriveSrc,
      arriveSize,
      arriveOption
    );

    //    
    var arrivePosition = new kakao.maps.LatLng(
      coordinates[coordinates.length - 1].lng,
      coordinates[coordinates.length - 1].lat
    );
    //   
    var arriveMarker = new kakao.maps.Marker({
      map: map, //      
      position: arrivePosition,
      image: arriveImage, //   
    });

    arriveMarker.setImage(arriveImage);

    //  ,   z-index 
    document.querySelector(
      "#map > div:nth-child(5) > div > div:nth-child(6) > div:nth-last-child(2)"
    ).style.zIndex = 3;
    document.querySelector(
      "#map > div:nth-child(5) > div > div:nth-child(6) > div:nth-last-child(1)"
    ).style.zIndex = 4;
  }

  function getTimeHTML(distance) {
    //    4km/h    67m/min
    var walkTime = (distance / 67) | 0;
    var walkHour = "",
      walkMin = "";

    //    60    
    if (walkTime > 60) {
      walkHour =
        '<span class="number">' + Math.floor(walkTime / 60) + "</span> ";
    }
    walkMin = '<span class="number">' + (walkTime % 60) + "</span> ";

    //    10km/h      167m/min
    var runningTime = (distance / 167) | 0;
    var runningHour = "",
      runningMin = "";

    //    60    
    if (runningTime > 60) {
      runningHour =
        '<span class="number">' + Math.floor(runningTime / 60) + "</span> ";
    }
    runningMin = '<span class="number">' + (runningTime % 60) + "</span> ";

    distance = Math.round((distance / 1000) * 100) / 100;
    //   ,    HTML Content  

    var content = '<ul class="dotOverlay distanceInfo">';
    content += "    <li>";
    content +=
      '        <span class="label"></span><span class="number">' +
      distance +
      " </span>km";
    content += "    </li>";
    content += "    <li>";
    content += '        <span class="label"></span>' + walkHour + walkMin;
    content += "    </li>";
    content += "    <li>";
    content +=
      '        <span class="label"></span>' + runningHour + runningMin;
    content += "    </li>";
    content += "    <li>";
    content += "        <span>** 10km/h  **</span>";
    content += "    </li>";
    content += "</ul>";

    var contentContainer = document.createElement("article");
    contentContainer.innerHTML = content;

    //   
    var courseTimeInfo = '<ul class="courseTimeInfo">';
    courseTimeInfo += "    <li>";
    courseTimeInfo +=
      '        <span class="label"><br></span><span class="number">' +
      distance +
      " </span>km";
    courseTimeInfo += "    </li>";
    courseTimeInfo += "    <li>";
    courseTimeInfo +=
      '        <span class="label"><br></span>' + walkHour + walkMin;
    courseTimeInfo += "    </li>";
    courseTimeInfo += "    <li>";
    courseTimeInfo +=
      '        <span class="label">(10km/h)<br></span>' +
      runningHour +
      runningMin;
    courseTimeInfo += "</ul>";

    const ul = document.createElement("div");
    ul.innerHTML = courseTimeInfo;
    document.querySelector(".result_text").prepend(ul);

    return contentContainer;
  }

  //  
  function toggleBtn() {
    document
      .querySelectorAll(
        "#map > div:nth-child(n) > div > div:nth-child(6) > div:nth-child(n) > div"
      )
      .forEach((elm) => elm.classList.toggle("toggle"));
  }

  function getLocation() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(showPosition, error, OPTIONS);
    } else {
      alert("Geolocation is not supported by this browser.");
    }
  }

  function error(err) {
    console.warn(`ERROR(${err.code}): ${err.message}`);
  }

  function showPosition(position) {
    la = position.coords.latitude;
    lo = position.coords.longitude;

    //   
    panTo();

    //     
    currentMarker();
  }

  function currentLocation() {
    //  
    getLocation();
  }

  function panTo() {
    //     
    var moveLatLon = new kakao.maps.LatLng(la, lo);

    CENTER_POINT = new kakao.maps.LatLng(la, lo);
    //    
    //          
    map.panTo(moveLatLon);
  }

  /*   */
  function currentMarker() {
    //   

    var imageSrc =
        "https://t1.daumcdn.net/localimg/localimages/07/2018/mw/m640/ico_marker.png", //  
      // var imageSrc = "./../img/circle-dot-solid.svg", //  
      imageSize = new kakao.maps.Size(30, 30), //  
      imageOption = { offset: new kakao.maps.Point(15, 15) }; //  .       .

    //      
    var markerImage = new kakao.maps.MarkerImage(
        imageSrc,
        imageSize,
        imageOption
      ),
      markerPosition = new kakao.maps.LatLng(la, lo); //   

    //  
    var marker = new kakao.maps.Marker({
      position: markerPosition,
      image: markerImage, //  
    });

    MARKER_BOX.push(marker);

    //    
    MARKER_BOX.forEach((elm) => elm.setMap(null));
    //     
    MARKER_BOX[MARKER_BOX.length - 1].setMap(map);
  }

  //           
  function setMapType(maptype) {
    var roadmapControl = document.getElementById("btnRoadmap");
    var skyviewControl = document.getElementById("btnSkyview");
    if (maptype === "roadmap") {
      map.setMapTypeId(kakao.maps.MapTypeId.ROADMAP);
      roadmapControl.className = "selected_btn";
      skyviewControl.className = "btn";
    } else {
      map.setMapTypeId(kakao.maps.MapTypeId.HYBRID);
      skyviewControl.className = "selected_btn";
      roadmapControl.className = "btn";
    }
  }

  //  ,         
  function zoomIn() {
    map.setLevel(map.getLevel() - 1);
  }

  //  ,         
  function zoomOut() {
    map.setLevel(map.getLevel() + 1);
  }

  //    
  window.addEventListener("resize", () => {
    if (map) {
      map.panTo(CENTER_POINT);
    }
  });

  return (
    <div ref={mapRef} id="map" className="map_style">
      <div className="pointToggleBtn map_btn_zIndex">
        <button
          id="toggle_btn"
          className="overlay_toggle_btn"
          onClick={toggleBtn}
        >
          
        </button>
      </div>
      {/*   */}
      <div className="currentLocationContainer map_btn_zIndex">
        <button className="current_location_btn" onClick={currentLocation}>
          <i className="fa-solid fa-location-crosshairs"></i>
        </button>
      </div>

      {/*     */}
      <div className="custom_typecontrol radius_border map_btn_zIndex">
        <span
          id="btnRoadmap"
          className="selected_btn"
          onClick={() => {
            setMapType("roadmap");
          }}
        >
          
        </span>
        <span
          id="btnSkyview"
          className="btn"
          onClick={() => {
            setMapType("skyview");
          }}
        >
          
        </span>
      </div>
      {/*  ,    */}
      <div className="custom_zoomcontrol radius_border map_btn_zIndex">
        <span onClick={zoomIn}>
          <LazyLoadImage
            src="https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/ico_plus.png"
            alt=""
          />
        </span>
        <span onClick={zoomOut}>
          <LazyLoadImage
            src="https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/ico_minus.png"
            alt=""
          />
        </span>
      </div>
    </div>
  );
};

export default KakaoMap;
